<template>
  <div>
              <!-- start page title -->
        <div class="row">
            <div class="col-12">
                <div class="page-title-box d-flex align-items-center justify-content-between">
                    <h4 class="mb-0 font-size-18">Cadastro de Centros de Custo</h4>
                    <div class="page-title-right">
                        <router-link :to="{name: 'centroCustoAdicionar'}" class="btn btn-outline-primary waves-effect waves-light">
                            <i class="bx bxs-plus-square font-size-16 align-middle mr-2"></i> Novo
                        </router-link>
                    </div>

                </div>
            </div>
        </div>
        <!-- end page title -->
        <div class="row" style="padding:10px">
        <div class="col-sm-12 col-md-3">                  
            <label >Mostrar  
            <select class="custom-select custom-select-sm form-control form-control-sm" v-model="filtro.per_page" @input="debounceBusca">
                <option :value="10">10</option>
                <option :value="25">25</option>
                <option :value="50">50</option>
                <option :value="100">100</option>
            </select>
            </label>                  
        </div>
        <div class="col-sm-12 col-md-3">
         &nbsp;       
        </div>
        <div class="col-sm-12 col-md-4">
            <label style="width:90%">Buscar por:
                <input autofocus type="text" class="form-control form-control-sm" placeholder="Codigo / Nome  / Descrição" v-model="filtro.texto" @input="debounceBusca" >
            </label>                  
        </div>
        <div class="col-sm-12 col-md-2">
            <label style="width:90%">Filtrar:
            <button @click="navegar(1)" class="btn btn-outline-primary btn-sm btn-block"><span class="bx bx-search-alt"></span></button>
            </label>
        </div>
        </div>
        <div class="row">
            <div class="col-12">
                <div class="card">
                    <div class="card-body">
                        
                        <div class="table-responsive">
                                <table class="table mb-0" >
                                <thead>
                                <tr>
                                    <th>Id</th>
                                    <th>Codigo</th>
                                    <th>Nome</th>
                                    <th>Descrição</th>
                                    <th>Ativo</th>
                                    <th>Ações</th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr v-for="registro in dados" :key="registro.id">
                                    <td>{{registro.id}}</td>
                                    <td class="text-right">{{registro.codigo}}</td>
                                    <td>{{registro.nome}}</td>
                                    <td>{{registro.descricao }}</td>
                                    <td>
                                        <span v-if="registro.ativo" class="badge badge-soft-primary">Sim</span>
                                        <span v-else class="badge badge-soft-dark">Não</span>
                                    </td>
                                    <td>
                                        <router-link :to="{name: 'centroCustoEditar', params: {id: registro.id }}" class="btn btn-outline-success waves-effect waves-light" style="padding: 3px 12px 3px 12px;" title="Editar">
                                            <i class="mdi mdi-pencil d-block font-size-12"></i>
                                        </router-link>

                                        <button v-if="registro.ativo" type="button" class="btn btn-outline-danger waves-effect waves-light" style="padding: 3px 12px 3px 12px;" title="Desativar"
                                                data-toggle="modal" data-target="#modal-delete" data-backdrop="static" @click="abrirModalDeletar(registro)">
                                            <i class="mdi mdi-trash-can d-block font-size-12"></i>
                                        </button>
                                    </td>
                                </tr>
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <td colspan="4"> 
                                            <paginacao-componente :source="paginacao" @navigate="navegar($event)"></paginacao-componente>
                                        </td>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>

                    </div>
                </div>
            </div> <!-- end col -->
        </div> <!-- end row -->
        <div class="modal fade" id="modal-delete" tabindex="-1" aria-labelledby="modal-delete-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-deleteTitle">  Confirmação de Desativação de Centro de Custos</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <div class="modal-body" v-if="objetoModal">
                        <p v-if="!sucessoDeletar">Tem certeza que deseja desativar o Centro de custo <strong> {{objetoModal.codigo}} </strong> ({{objetoModal.nome}}) ?</p>
                        
                        <div v-if="sucessoDeletar" class="text-center" >
                            <div class="swal2-icon swal2-success swal2-icon-show" style="display: flex;">
                                <div class="swal2-success-circular-line-left" style="background-color: rgb(255, 255, 255);"></div>
                                <span class="swal2-success-line-tip"></span> <span class="swal2-success-line-long"></span>
                                <div class="swal2-success-ring"></div> <div class="swal2-success-fix" style="background-color: rgb(255, 255, 255);"></div>
                                <div class="swal2-success-circular-line-right" style="background-color: rgb(255, 255, 255);"></div>
                            </div>
                            <p class="success text-center font-size-18">{{sucessoDeletar}}</p>
                            
                        </div>
                        <mensagem-componente titulo="Erro!" v-if="erroDeletar" :erros="erroDeletar" @fechar="erroDeletar = null"></mensagem-componente>
                    </div>
                    <div v-else class="modal-body"></div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" :disabled="sucessoDeletar || !objetoModal" @click.prevent="deletar()"><i class="mdi mdi-trash-can font-size-18"></i> Desativar </button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                    </div>
                </div><!-- /.modal-content -->
            </div><!-- /.modal-dialog -->
        </div>
  </div>
</template>

<script>
import mensagem from '../../../components/forms/mensagem.vue';

import { CentroCustoService } from '../../../services'

const centroCustoService = new CentroCustoService();

export default {
  components: { mensagem },

    data() {
        return {
            dados: [],
            filtro:{
                ordenar: 'codigo',
                ordem: 'asc',
                page: 1,
                per_page: 25,
                texto: '',
                tipo: ''
            },
            paginacao: {
                current_page: 0,
                total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 25
            },
            debounce: null,
            objetoModal: null,
            sucessoDeletar: null,
            erroDeletar: null
        };
    },
    mounted() {
            this.buscar();
    },
    methods: {

        abrirModalDeletar: function(obj){
            this.sucessoDeletar = null;
            this.erroDeletar = null;
            this.objetoModal = {...obj};
        },

        deletar: function(){
            this.sucessoDeletar = null;
            this.erroDeletar = null;

            centroCustoService.deletar(this.objetoModal.id)
            .then( response => {
                let i = this.dados.map(x => x.id).indexOf(this.objetoModal.id);
                this.dados[i].ativo = false;
                this.sucessoDeletar = 'Registro removido com sucesso!';
            }).catch( erro => this.erroDeletar = erro)            
        },

        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },
        
        buscar:function(){
            centroCustoService.listar(this.filtro).then(
                response => {
                    this.$data.dados = response.data.data;
                    this.$data.paginacao = response.data;
                    this.$data.filtro.page = response.data.current_page;
                    this.$data.paginacao.per_page = response.data.per_page;
                }
            )
        },

        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },
  
    }
};
</script>
<style scoped>
    tr:hover{
        background-color: #eff2f7 ;
    }
</style>
